import React from 'react';
import preloader from './../../../assets/images/preloader.svg';

let Preloader = (props) => {
    return (
        <div style={{backgroundColor: 'white', position: 'absolute', top: 0, left: 0, opacity: '0.5'}}>
            <img width='60' src={preloader} alt='' />
        </div>
    )
}

export default Preloader;