import React from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import { BrowserRouter, Route, withRouter } from 'react-router-dom';
import UsersContainer from './components/Users/UsersContainer';
// import ProfileContainer from './components/Profile/ProfileContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import LoginPage from './components/Login/Login';
import { connect, Provider } from 'react-redux';
import { compose } from 'redux';
import {initializeApp} from './redux/app-reducer';
import Preloader from './components/common/Preloader/Preloader';
import store from './redux/redux-store';
import { withSuspense } from './hoc/withSuspense';

// import DialogsContainer from './components/Dialogs/DialogsContainer';
const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'))
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'))


// Реакт компонент - возвращает jsx
class App extends React.Component {
    componentDidMount() {
        this.props.initializeApp()
    }

    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }

        return (
            <div className = 'app-wrapper' >
                <HeaderContainer />
                <Navbar />
                {/* <Friends state={props.state.friendsPage} /> */}
    
                <div className='app-wrapper-content'>
                    {/* когда роутер увидет совпадение пути, он загрузит код (функцию возвращающую jsx) */}
                    <Route 
                        path='/dialogs' 
                        render={ withSuspense(DialogsContainer) } />
    
                    <Route 
                        path='/profile/:userId?' 
                        render={ withSuspense(ProfileContainer) } />
    
                    <Route 
                        path='/users'
                        render={ () => <UsersContainer /> } />
    
                    <Route 
                        path='/login'
                        render={ () => <LoginPage /> } />
                    {/*
                        когда роутер увидет совпадение пути, он загрузит указанный компонент
                        <Route path='/profile' component={Profile} />
                        <Route path='/dialogs' component={Dialogs} /> */}
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized
})

let AppContainer = compose(
    withRouter, 
    connect(mapStateToProps, {initializeApp})) (App);

const SamuraiJSApp = (props) => {
    return <BrowserRouter>
                <Provider store={store}>
                    <AppContainer />
                </Provider>
            </BrowserRouter>
}

export default SamuraiJSApp;