// выносим в отдельный объект респонсибилити, связанную с взаимодействием с сервером (условный DataAccessLayer) 
// и устраняем дублирование засчёт использования инстанса (экземпляра) axios, который можно создать с помощью axios.create

import * as axios from "axios";
import { follow, unfollow } from "../redux/users-reducer";

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        'API-KEY': '713b85ae-4344-4daa-8594-7f2c9ff7243f'
    }
})

export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 10) {
        // вернет промис
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            // вернем промис только с data
            .then((response) => response.data)
    },
    follow(userId) {
        return instance.post(`follow/${userId}`)
    },
    unfollow(userId) {
        return instance.delete(`follow/${userId}`)
    },
    getProfile(userId) {
        console.warn('Устаревший метод. Используйте объект profileAPI.')
        return profileAPI.getProfile(userId)
    }
}

export const profileAPI = {
    getProfile(userId) {
        return instance.get(`profile/${userId}`)
    },
    getStatus(userId) {
        return instance.get(`profile/status/${userId}`)
    },
    updateStatus(status) {
        return instance.put(`profile/status`, {status: status})
    }
}

export const authAPI = {
    me() {
        return instance.get(`auth/me`)
    },
    login(email, password, rememberMe = false) {
        return instance.post(`auth/login`, {email, password, rememberMe})
    },
    logout() {
        return instance.delete(`auth/login`)
    }
}